.product-item-list {
	
	&.row {
		margin-left: ceil(($grid-gutter-width / -8));
		margin-right: ceil(($grid-gutter-width / -8));
		
		& > .col {
			padding-left: ceil(($grid-gutter-width / 8));
			padding-right: ceil(($grid-gutter-width / 8));
		}
	}
	
	@media (min-width: $screen-sm-min){
		&.row {
			margin-left: ceil(($grid-gutter-width / -4));
			margin-right: ceil(($grid-gutter-width / -4));
			
			& > .col {
				padding-left: ceil(($grid-gutter-width / 4));
				padding-right: ceil(($grid-gutter-width / 4));
			}
		}
	}
}