%color-item {

  &__btn {
    background: $body-bg;
    border: 2px solid transparent;
    border-radius: 50%;
    color: $text-color;
    cursor: pointer;
    display: inline-block;
    font-weight: normal;
    padding: 2px;
    position: relative;
    text-align: center;
    text-decoration: none;
    transition: border .3s ease, color .3s ease;
    vertical-align: middle;
  }
  
  &__icon {
    background-size: cover;
    border: 1px solid #d7dbdd;
    border-radius: 50%;
    display: inline-block;
    line-height: $line-height-computed*1.6;
    width: $line-height-computed*1.6;
    height: $line-height-computed*1.6;
    vertical-align: middle;    
  }

  &.checked &__btn,
  &:hover &__btn,
  &__btn:active,
  &__btn:focus {
    border-color: $brand-primary;
  }

  &.disabled > &__btn {
    background: transparent;
    border-color: transparent;
    box-shadow: none;
    cursor: inherit;
    
    &:before {
      position: absolute;
      bottom: 0;
      right: 0;
      top: 0;
      left: 0;
      content: ' ';
      display: block;
      z-index: 100;
      background: url("data:image/png;base64,") no-repeat center;
      opacity: .7;
    }
  }
  
  &.disabled &__icon {
    opacity: .2;
  }
}